.profile{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 40px;
    border: 1px solid #000;
    box-shadow: grey 0px 0px 10px;
    width: 300px;
    height: 300px;
}

.description{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.avatar{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid grey;
}

.name{
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 0;
}

.tag{
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 0;
    color: gray;
}

.location{
    font-size: 1rem;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 0;
    color: gray;
}

.stats{
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    margin-top: 15px;
    padding-left: 0;
    margin-bottom: 0;
    border-top: #000 1px solid;
    width: 100%;
}

.stats li{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 93px;
}

.stats li:not(:last-child){
    margin-right: 10px;
}

.label{
    font-size: 0.8rem;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 0;
}

.quantity{
    font-size: 1.2rem;
    font-weight: bold;
    margin-top: 10px;
    margin-bottom: 0;
}